import React from 'react';

const Context = React.createContext();

export const reducer = (state, action) => {
  const { type } = action;

  switch (type) {
    case 'RESET':
      return {
        count: 1,
        current: 0,
      };

    case 'NEXT_SLIDE':
      return {
        ...state,
        current: Math.min(state.count - 1, Math.max(0, state.current + 1)),
      };
    case 'PREVIOUS_SLIDE':
      return {
        ...state,
        current: Math.min(state.count - 1, Math.max(0, state.current - 1)),
      };

    default:
      return state;
  }
};

export const initializer = (slides) => {
  const state = {};
  state.current = 0;
  state.count = slides.length ? slides.length : 1;
  return state;
};

// eslint-disable-next-line no-unused-vars
export const makeActions = (dispatch, slides, state) => {
  const fns = {};

  fns.onNextSlide = () => {
    dispatch({ type: 'NEXT_SLIDE' });
  };

  fns.onPreviousSlide = () => {
    dispatch({ type: 'PREVIOUS_SLIDE' });
  };

  return fns;
};

// eslint-disable-next-line react/prop-types
const SlidesProvider = ({ children, slides }) => {
  const [state, dispatch] = React.useReducer(reducer, slides, initializer);
  const actions = makeActions(dispatch, slides, state);

  return (
    <Context.Provider
      value={{
        slides,
        ...actions,
        ...state,
      }}
    >
      {children}
    </Context.Provider>
  );
};

export default SlidesProvider;

export const useSlides = () => React.useContext(Context);
