/* eslint-disable react/destructuring-assignment, react/prop-types, react/jsx-props-no-spreading */
import React from 'react';
import { createUseStyles } from 'react-jss';

import { useSlides } from './Context';

import Button from '../Button';

const useStyles = createUseStyles({
  root: {
    display: 'flex',
    justifyContent: 'space-between',
    margin: 'auto',
  },
  prev: ({ current }) => ({
    opacity: current ? 1 : 0,
    pointerEvents: current ? 'all' : 'none',
  }),
  next: ({ current, count }) => ({
    opacity: current < count - 1 ? 1 : 0,
    pointerEvents: current < count - 1 ? 'all' : 'none',
  }),
}, { name: 'SlidesControls' });

const Controls = (props) => {
  const slides = useSlides();
  const classes = useStyles({ ...props, ...slides });

  return (
    <div className={[props.className, classes.root].filter(Boolean).join(' ')}>
      <Button
        className={[classes.button, classes.prev].join(' ')}
        onClick={slides.onPreviousSlide}
      >
        Previous
      </Button>

      <Button
        className={[classes.button, classes.next].join(' ')}
        onClick={slides.onNextSlide}
      >
        Next
      </Button>
    </div>
  );
};

export default Controls;
