/* eslint-disable react/destructuring-assignment, react/prop-types, react/jsx-props-no-spreading */
import React from 'react';
import { createUseStyles } from 'react-jss';
import { useEpisode } from '../Episode/Context';
import SlidesProvider, { useSlides } from './Context';

import Controls from './Controls';

export { default as TypistSlide } from './TypistSlide';
export { default as Slide } from './Slide';
export { default as TitleSlide } from './TitleSlide';

const useStyles = createUseStyles({
  root: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-between',
    height: '60vh',
    maxHeight: 750,
  },

  children: {
    flexGrow: 1,
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
  },

  controls: {
    width: '80vw',
    maxWidth: 750,
  },
}, { name: 'Slides' });

const Slides = ({ children, ...props }) => {
  const slides = useSlides();
  const episode = useEpisode();
  const { current, count } = slides;
  const classes = useStyles({ ...props, ...episode, ...slides });

  React.useEffect(() => {
    episode.onStepProgress(current, count);
  }, [current, count]);

  return (
    <div className={[props.className, classes.root].filter(Boolean).join(' ')}>
      <div className={classes.children}>
        {children[slides.current] || children}
      </div>

      <Controls className={classes.controls} />
    </div>
  );
};

const withSlides = (Comp) => {
  const WithSlides = ({ children, ...props }) => (
    <SlidesProvider slides={children}>
      <Comp {...props}>
        {children}
      </Comp>
    </SlidesProvider>
  );
  WithSlides.displayName = `${Comp.displayName || Comp.name || 'WTF'}SlidesProvider`;
  return WithSlides;
};

export default withSlides(Slides);
