import React from 'react';
import PropTypes from 'prop-types';
import { createUseStyles } from 'react-jss';

import useSoundTrigger, { spriteMap } from '../hooks/useSoundTrigger';

const useStyles = createUseStyles({
  root: (props) => ({
    display: 'inline-flex',
    color: 'inherit',
    border: '1px solid currentColor',
    transition: 'all 218ms ease-in',
    justifyContent: 'center',
    alignItems: 'center',
    cursor: props.disabled ? 'default' : 'pointer',
    opacity: (props.disabled && 0.5) || 1,

    backgroundSize: '100% 100%',
    background: 'linear-gradient(lime, transparent 38%, transparent 60%, lime)',
    boxShadow: '0 0 0 0 lime',
    padding: '0.65em 1em',
    borderRadius: '3em',
    textTransform: 'lowercase',
    fontSize: '1.2rem',
    textDecoration: 'none',
    fontFamily: 'KRSNA-DREAMER',
    fontWeight: 700,
    '&:link,&:hover,&:focus': {
      textDecoration: 'none',
      outline: 'none',
    },
    '&:hover,&:focus': {
      boxShadow: '0 0 0px 3px lime',
      backgroundSize: '100% 100%',
      background: 'linear-gradient(lime, transparent 22%, transparent 80%, lime)',
    },
    // visibility: !props.hidden ? 'visible' : 'hidden',
  }),
}, { name: 'Button' });

const Button = ({
  children,
  component: Component,
  ...props
}) => {
  const classes = useStyles(props);
  const {
    className,
  } = props;

  const soundProps = useSoundTrigger(props);

  return (
    <Component
      // eslint-disable-next-line react/jsx-props-no-spreading
      {...soundProps}
      className={[
        className,
        classes.root,
      ].filter(Boolean).join(' ')}
    >
      {children}
    </Component>
  );
};

Button.propTypes = {
  children: PropTypes.node.isRequired,
  className: PropTypes.string,
  type: PropTypes.oneOf(['button', 'submit', 'reset']),
  component: PropTypes.oneOfType([
    PropTypes.elementType,
    PropTypes.string,
  ]),
  onClick: PropTypes.func,
  onMouseEnter: PropTypes.func,
  clickSound: PropTypes.oneOf(Object.keys(spriteMap)),
  enterSound: PropTypes.oneOf(Object.keys(spriteMap)),
};

Button.defaultProps = {
  className: null,
  component: 'button',
  type: 'button',
  onClick: () => { },
  onMouseEnter: () => { },
  clickSound: 'laser2',
  enterSound: 'laser1',
};

export default Button;
