import React from 'react';
import PropTypes from 'prop-types';
import { createUseStyles } from 'react-jss';

const useStyles = createUseStyles({
  root: {
    textAlign: 'center',
    color: '#62FF00',
    '& h2, & h1': {
      margin: 'auto',
      lineHeight: '1em',

      '& a': {
        display: 'none',
      },
    },
    '& h2': {
      font: 'normal normal normal 1.7rem / 76px Libre Baskerville',
      textShadow: '0px 0px 8px #0000008E',
      textTransform: 'uppercase',
    },
    '& h1': {
      font: 'normal normal normal 3.2rem KRSNA-DREAMER',
      textShadow: '0px 0px 24px #000000B1',
      textTransform: 'lowercase',
    },
  },
}, { name: 'TitleSlide' });

const TitleSlide = ({ children, component: Comp, ...props }) => {
  const { className } = props;
  const classes = useStyles(props);
  return (
    <Comp
      // eslint-disable-next-line react/jsx-props-no-spreading
      {...props}
      className={[className, classes.root].filter(Boolean).join(' ')}
    >
      {children}
    </Comp>
  );
};

TitleSlide.propTypes = {
  children: PropTypes.node.isRequired,
  component: PropTypes.oneOfType([
    PropTypes.elementType,
    PropTypes.string,
  ]),
  className: PropTypes.string,
};

TitleSlide.defaultProps = {
  component: 'div',
  className: null,
};

export default TitleSlide;
