/* eslint-disable react/no-unused-prop-types */
import React from 'react';
import PropTypes from 'prop-types';
import { createUseStyles } from 'react-jss';

const glow = {
  anim: {
    from: {
      opacity: 1,
    },
    to: {
      opacity: 0,
    },
  },
  mixin: {
    animationDirection: 'alternate',
    animationName: '$anim-glow',
    animationDuration: '5s',
    animationIterationCount: 'infinite',
  },
};

const useLayerStyles = createUseStyles({
  '@keyframes anim-glow': glow.anim,

  root: (props) => ({
    pointerEvents: 'none',
    position: 'fixed',
    cursorPointer: 'none',
    zIndex: (props.position === 'above' ? 2000 : -1000) + (props.index || 0),
    top: 0,
    right: 0,
    bottom: 0,
    left: 0,
    backgroundPosition: 'center',
    ...(props.style || {}),
    ...((props.glow && glow.mixin) || {}),
  }),
}, { name: 'layoutLayer' });

const Layer = (props) => {
  // const {
  //   position = 'above',
  //   srcSets,
  // } = props;
  const classes = useLayerStyles({ ...props });
  // console.info(position, classes, srcSets);
  return (
    <div className={classes.root} />
  );
};

Layer.propTypes = {
  onEnter: PropTypes.func,
  onStart: PropTypes.func,
  onFinished: PropTypes.func,
  onExit: PropTypes.func,
  position: PropTypes.oneOf([
    'fixed',
    'absolute',
    'relative',
  ]),
  glow: PropTypes.bool,
  style: PropTypes.objectOf(PropTypes.any),
  className: PropTypes.string,
  above: PropTypes.bool,
  index: PropTypes.number,
};

Layer.defaultProps = {
  onEnter: null,
  onStart: null,
  onFinished: null,
  onExit: null,
  position: 'fixed',
  glow: false,
  style: {},
  className: null,
  above: false,
  index: 0,
};

export default Layer;
