import React from 'react';

const Context = React.createContext();

const mdxObject = (episodeMdx) => episodeMdx.nodes.reduce((obj, {
  parent: { name },
  frontmatter: { title, choices, showChoices = false },
  body,
}) => ({
  ...obj,
  [name]: {
    id: name,
    title,
    body,
    showChoices,
    choices: choices || [],
  },
}), {});

export const reducer = (state, action) => {
  const { type, payload } = action;

  switch (type) {
    case 'MAKE_CHOICE':
      return {
        ...state,
        choices: [...state.choices, payload.id],
        step: { ...payload },
      };

    case 'STEP_PROGRESS':
      return {
        ...state,
        size: payload.size ?? state.size,
        progress: payload.progress,
      };

    default:
      return state;
  }
};

export const initializer = (steps) => {
  const state = {};
  state.choices = [];
  state.progress = 0;
  state.size = 1;
  state.step = { ...steps.intro };
  return state;
};

// eslint-disable-next-line no-unused-vars
export const makeActions = (dispatch, steps, state) => {
  const fns = {};

  fns.onMakeChoice = (id) => {
    const step = steps[id];
    if (!step) throw new Error(`Step ${id} does not exists`);
    dispatch({ type: 'MAKE_CHOICE', payload: step });
  };

  fns.onStepProgress = (progress, size = state.size) => {
    if (progress < 0 || progress > size) throw new Error(`Progress ${progress} is out of range (0 - ${size})`);

    if (progress === state.progress && size === state.size) return;

    dispatch({ type: 'STEP_PROGRESS', payload: { progress, size } });
  };

  fns.onStepSize = (size) => {
    dispatch({ type: 'STEP_PROGRESS', payload: { progress: 0, size } });
  };

  return fns;
};

// eslint-disable-next-line react/prop-types
const EpisodeProvider = ({ children, data }) => {
  const steps = mdxObject(data);
  const [state, dispatch] = React.useReducer(reducer, steps, initializer);
  const actions = makeActions(dispatch, steps, state);

  return (
    <Context.Provider
      value={{
        steps,
        ...actions,
        ...state,
      }}
    >
      {children}
    </Context.Provider>
  );
};

export default EpisodeProvider;

export const useEpisode = () => React.useContext(Context);
