const Link = ({
  children,
  ...props
}) => {
  const {
    to,
    color,
    className,
  } = props;

  const soundProps = useSoundTrigger(props);

  return (
    <AniLink
      data-mk-link
      paintDrip
      hex={theme[color] || color}
      to={to}
      onClick={soundProps.onClick}
      onMouseEnter={soundProps.onMouseEnter}
      className={[
        className,
        classes.root,
        classes[color],
      ].filter(Boolean).join(' ')}
    >
      {children}
    </AniLink>
  );
};

Link.displayName = 'MKLink';

Link.propTypes = {
  children: PropTypes.node.isRequired,
  to: PropTypes.string.isRequired,
  color: PropTypes.string,
  className: PropTypes.string,
  onClick: PropTypes.func,
  onMouseEnter: PropTypes.func,
  clickSound: PropTypes.oneOf(Object.keys(spriteMap)),
  enterSound: PropTypes.oneOf(Object.keys(spriteMap)),
};

Link.defaultProps = {
  color: 'primary',
  className: null,
  onClick: () => { },
  onMouseEnter: () => { },
  clickSound: 'laser2',
  enterSound: 'laser1',
};

export default Link;
