import React from 'react';
import PropTypes from 'prop-types';
import { useStaticQuery, graphql } from 'gatsby';

import Access from './Access';
import Layer from '../Layer/Layer';
import './Layout.scss';
import styles from './Layout.module.scss';
import useResize from '../../hooks/useResize';
import VisualGrid from './VisualGrid';
import { Provider as SoundProvider } from '../SoundManager/SoundManager';
import Header from './Header';

const passThru = typeof window !== 'undefined' && window.location.host.includes('localhost');

const normalizeSrcSets = ({
  allFile: { nodes = [] } = {},
}) => nodes.reduce((obj, {
  childImageSharp: {
    fluid: { srcSet = null } = {},
  } = {},
  name,
  ...rest
} = {}) => ({
  ...obj,
  [name]: {
    ...rest,
    name,
    srcSet,
  },
}), {});

// eslint-disable-next-line react/prop-types
const LayoutBase = ({ children, noHeader }) => (
  <>
    {!noHeader && (<Header />)}
    {children}
  </>
);

// eslint-disable-next-line no-unused-vars
const Layout = ({ children, wide = false, noHeader = false }) => {
  // const [visible, setVisible] = React.useState(1);
  // const handleMouseMove = () => !visible && setVisible(true);
  // React.useEffect(() => {
  //   const timer = setTimeout(() => setVisible(false), 1500);
  //   window.addEventListener('mousemove', handleMouseMove, { passive: true });
  //   return () => {
  //     clearTimeout(timer);
  //     window.removeEventListener('mousemove', handleMouseMove, { passive: true });
  //   };
  // });
  // const visible = false;

  const size = useResize();
  const data = useStaticQuery(graphql`{
  allFile(filter: {extension: {in: ["png", "jpg", "jpeg"]}, dir: {regex: "/\\/src\\/images/"}}) {
    nodes {
      relativePath
      relativeDirectory
      root
      dir
      name
      extension
      childImageSharp {
        fluid {
          srcSet
        }
      }
    }
  }
  site {
    siteMetadata {
      title
    }
  }
}`);

  const srcSets = normalizeSrcSets(data);
  const parseSrcSet = (name) => srcSets[name].srcSet
    .split(',')
    .map((s) => s.trim())
    .reduce((acc, val) => {
      const [src, sizeStr] = val.split(' ');
      return {
        ...acc,
        [sizeStr]: src,
      };
    }, {});

  const findSrc = (srcSet) => {
    const keys = Object.keys(srcSet);
    const srcKey = keys.find((key, k) => {
      const sideSize = parseInt(key, 10);
      const side = key[key.length - 1] !== 'w' ? 'height' : 'width';
      return sideSize > size[side] && keys[k - 1];
    });
    return srcSet[srcKey];
  };

  const topLayerSrc = findSrc(parseSrcSet('MKS_ambiant_BG_Stars_001'));

  return (
    <SoundProvider data={{}}>
      <LayoutBase
        data={data}
        noHeader={noHeader}
        className={[wide && styles.wide, styles.root].filter(Boolean).join(' ')}
      >
        <Access
          passThru={passThru}
        >
          {/* <div className={styles.rootOverlay} />

          <div
            className={[
              styles.innerlay,
              visible && styles.innerlayVisible,
            ].filter(Boolean).join(' ')}
          >
            <div
              className={[
                styles.overlay,
                visible && styles.overlayVisible,
              ].filter(Boolean).join(' ')}
            >
              {!noHeader && (
                <Header
                  className={styles.haeder}
                  siteTitle={data.site.siteMetadata.title}
                />
              )}

              <div className={styles.menu}>
                <nav>
                  <ul className={styles.menuLinks}>
                    <li className={styles.menuLink}>
                      <Link color="secondary" to="/episodes">
                        Episodes
                      </Link>
                    </li>
                  </ul>
                </nav>
              </div>

              <Footer />
            </div>
          </div> */}

          <main
            className={[wide && styles.wide, styles.main].filter(Boolean).join(' ')}
          >
            {children}
          </main>

          {/*
          <div className={styles.rootUnderlay} />

          <VisualGrid /> */}
        </Access>
      </LayoutBase>

      <Layer
        style={{
          transform: 'scale(-1, -1)',
          backgroundSize: 'cover',
          mixBlendMode: 'overlay',
          animationDelay: '3s',
          backgroundImage: `url(${topLayerSrc})`,
        }}
        index={1}
        glow
      />
      <Layer
        style={{
          backgroundSize: 'cover',
          mixBlendMode: 'multiply',
          backgroundImage: `url(${topLayerSrc})`,
        }}
        index={1}
        glow
      />
      <Layer
        style={{
          backgroundSize: 'cover',
          backgroundImage: `url(${topLayerSrc})`,
          animationDelay: '-2s',
        }}
        glow
      />
      <VisualGrid />
    </SoundProvider>
  );
};

Layout.propTypes = {
  children: PropTypes.node.isRequired,
  noHeader: PropTypes.bool,
  wide: PropTypes.bool,
};

Layout.defaultProps = {
  noHeader: false,
  wide: false,
};

Layout.displayName = 'MKLayout';

export default Layout;
