/* eslint-disable react/no-unused-prop-types, react/destructuring-assignment */
import React from 'react';
import PropTypes from 'prop-types';
import Typist from 'react-typist';

import Card from '../Card/Card';
import styles from './TypistSlide.module.scss';
import { useSlides } from './Context';

const TypistSlide = (props) => {
  const {
    className,
    children,
    startDelay,
    nextDelay,
  } = props;
  const { onNextSlide } = useSlides();
  const [currentChild, setCurrentChild] = React.useState(0);

  const handleTypingDone = React.useCallback(() => setCurrentChild((prev) => {
    const count = children && children.length ? children.length : 1;
    if (prev + 1 === count) {
      setTimeout(onNextSlide, nextDelay);
      return prev;
    }
    // should not happen...
    if (prev + 1 > count) {
      return prev;
    }
    return prev + 1;
  }), [children, nextDelay, onNextSlide]);

  let content = null;
  if (!Array.isArray(children)) {
    content = (
      <Typist
        startDelay={startDelay}
        onTypingDone={handleTypingDone}
        className={styles.slide__typist}
        // cursor={{
        //   hideWhenDone: true,
        //   element: ' ',
        // }}
      >
        {children}
      </Typist>
    );
  } else {
    content = [
      ...children.slice(0, currentChild),
      (
        <Typist
          startDelay={currentChild === 0 ? startDelay : 0}
          key={`typist-${currentChild}`}
          className={styles.slide__typist}
          onTypingDone={handleTypingDone}
          // cursor={{
          //   // hideWhenDone: true,
          //   element: ' ',
          // }}
        >
          {children[currentChild]}
        </Typist>
      ),
    ];
  }

  const classNames = [
    className,
    styles.slideTypist,
  ];
  return (
    <div className={styles.slide}>
      <Card className={classNames.filter(Boolean).join(' ')}>
        {content}
      </Card>
    </div>
  );
};

TypistSlide.displayName = 'MKTypistSlide';

TypistSlide.propTypes = {
  startDelay: PropTypes.number,
  nextDelay: PropTypes.number,
  className: PropTypes.string,
  children: PropTypes.node,
};

TypistSlide.defaultProps = {
  startDelay: 1000,
  nextDelay: 1000,
  className: null,
  children: null,
};

export default TypistSlide;
