import React from 'react';
import useSound from 'use-sound';

import soundAsset, { spriteMap } from '../episodes/0/0/assets/soundsButton';

const sprite = Object.keys(spriteMap).reduce((obj, key) => {
  const [start, end] = spriteMap[key];
  return {
    ...obj,
    [key]: [start, end - start],
  };
}, {});

const useSoundTrigger = ({
  onClick,
  clickSound,
  onMouseEnter,
  enterSound,
  ...props
}) => {
  const [loaded, setLoaded] = React.useState(false);

  const [play] = useSound(soundAsset, {
    sprite,
    // onerror: () => { console.info('onerror'); },
    // onend: () => { console.info('onend'); },
    onload: () => setLoaded(true),
  });

  const handleClick = React.useCallback((...args) => {
    if (loaded && clickSound && sprite[clickSound]) play({ id: clickSound });
    return onClick(...args);
  }, [sprite, loaded, onClick, clickSound]);

  const handleMouseEnter = React.useCallback((...args) => {
    if (loaded && enterSound && sprite[enterSound]) play({ id: enterSound });
    return onMouseEnter(...args);
  }, [sprite, loaded, onMouseEnter, enterSound]);

  return {
    ...props,
    onClick: handleClick,
    onMouseEnter: handleMouseEnter,
  };
};

export default useSoundTrigger;

export { spriteMap };
