import React from 'react';

const Context = React.createContext();

// eslint-disable-next-line
const SoundManagerProvider = ({ children, data }) => {
  const audioContext = {};

  return (
    <Context.Provider value={audioContext}>
      {children}
    </Context.Provider>
  );
};

export default SoundManagerProvider;

export const useSoundManager = () => React.useContext(Context);
