import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';

import styles from './Header.module.scss';
import Link from '../Link';

const Logo = () => {
  const data = useStaticQuery(graphql`
    {
      file(name: {eq: "MKS_LOGO_GR_1980"}) {
        childImageSharp {
          fixed(width: 160) {
            src
            srcSet
          }
        }
      }
    }
  `);
  return (
    <img
      alt="METAKRONISMS"
      src={data?.file?.childImageSharp?.fixed?.src}
      srcSet={data?.file?.childImageSharp?.fixed?.srcSet}
    />
  );
};

const Header = () => (
  <header className={styles.root}>
    <Link color="primary" to="/" className={styles.homeLink}>
      <Logo />
    </Link>
    <nav className={styles.nav}>
      <ul className={styles.navList}>
        <li className={styles.navItem}>
          <Link
            color="primary"
            to="/episodes/s0/e0"
            className={[styles.navLink, styles.navLinkActive].join(' ')}
          >
            Play
          </Link>
        </li>

        <li className={styles.navItem}>
          <Link
            color="primary"
            to="/concept"
            className={[styles.navLink].join(' ')}
          >
            concept
          </Link>
        </li>

        {/*
        <li className={styles.navItem}>
          <Link
            color="primary"
            to="/join"
            className={[styles.navLink].join(' ')}
          >
            join
          </Link>
        </li>
        */}

        <li className={styles.navItem}>
          <Link
            color="primary"
            to="/about"
            className={[styles.navLink].join(' ')}
          >
            about
          </Link>
        </li>

        <li className={styles.navItem}>
          <Link
            color="primary"
            to="/contact"
            className={[styles.navLink].join(' ')}
          >
            contact
          </Link>
        </li>
      </ul>
    </nav>
  </header>
);

Header.displayName = 'MKHeader';

export default Header;
